/* App.css */

.App {
  text-align: center;
  font-family: Arial, sans-serif;
  padding: 40px;
  background-color: #f9f9f9;
}

h1 {
  color: #b32e2e;
}

p {
  font-size: 18px;
  color: #333;
}
form {
  max-width: 400px;
  margin: 30px auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

input, textarea {
  padding: 10px;
  font-size: 16px;
}

button {
  background-color: #b32e2e;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 4px;
}

/* Filtre menülerinin stili */
select {
  padding: 10px;
  margin: 10px;
  font-size: 1rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  width: 200px;
}

input[type="text"] {
  width: 100%;
  max-width: 400px;
  padding: 10px;
  margin: 20px auto;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 1rem;
  display: block;
}

.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px;
}

.drink-card {
  width: 100%;
  background-color: #f8f8f8;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.drink-card:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
}

.drink-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.card-content {
  padding: 15px;
}

.card-content h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.card-content p {
  font-size: 1rem;
  margin-bottom: 5px;
}

/* İçki kartı butonları */
button {
  padding: 8px 12px;
  margin: 5px;
  font-size: 1rem;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #eee;
}

/* Silme ve Düzenleme butonlarına özel stil */
button:nth-child(1) {
  background-color: #f44336;
  color: white;
}

button:nth-child(2) {
  background-color: #4CAF50;
  color: white;
}

/* Temel stil ayarları */

body.light {
  background-color: #f9f9f9;
  color: #333;
}

body.dark {
  background-color: #1e1e1e;
  color: #f1f1f1;
}

/* Diğer stil ayarları */
button {
  padding: 8px;
  font-size: 16px;
  cursor: pointer;
}

input, select {
  padding: 8px;
  margin: 5px;
}

button:hover {
  background-color: #ccc;
}

/* Tema geçişinin düzgün olması için geçiş efekti ekleyin */
body,
button,
input,
select {
  transition: background-color 0.3s ease, color 0.3s ease;
}


/* İçki adı, marka ve diğer input/select alanlarının boyutlarını küçültme */
input[type="text"],
select,
textarea {
  padding: 8px;
  font-size: 14px; /* Küçük yazı tipi */
  width: 100%; /* Genişlik %100 olacak şekilde ayarlandı */
  max-width: 350px; /* Ekranda daha küçük yer kaplaması için max-width ekledik */
  margin: 5px auto; /* Etrafındaki boşlukları ayarladık */
  border-radius: 8px;
  border: 1px solid #ccc;
  display: block; /* Blok element olarak hizalandı */
}

/* İlgili input alanları için daha fazla stil */
input[type="text"]:focus,
select:focus {
  border-color: #b32e2e; /* Fokuslanınca sınır rengini değiştirdik */
  box-shadow: 0 0 8px rgba(179, 46, 46, 0.3);
}

/* Button ve genel butonların stilini daha küçük yapma */
button {
  padding: 8px 12px;
  font-size: 14px; /* Daha küçük yazı tipi */
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #eee;
}

/* Arama ve filtreleme kutularının hizalanmasını düzenlemek için flexbox kullanma */
.search-container {
  display: flex;
  justify-content: center; /* Yatayda ortalar */
  align-items: center; /* Dikeyde ortalar */
  flex-wrap: wrap; /* Mobil uyumluluk için satır başına sarmasını sağlar */
  gap: 10px; /* Kutular arasında boşluk bırakır */
  margin-bottom: 20px; /* Alt boşluk ekler */
}

/* İçki adı, marka ve diğer input/select alanlarının boyutlarını küçültme */
input[type="text"],
select {
  padding: 8px;
  font-size: 14px;
  max-width: 350px; /* Genişliği sınırlandırıyoruz */
  width: 100%;
  margin: 5px;
  border-radius: 8px;
  border: 1px solid #ccc;
  display: inline-block;
}

/* Filtre menülerinin hizalanması */
select {
  width: 200px;
  max-width: 100%; /* Genişlik sınırlandırmasını %100 yapar */
}

textarea {
  width: 100%;
  padding: 10px;
  font-size: 1rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-top: 10px;
  margin-bottom: 10px;
}

button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}

.message-box {
  padding: 12px;
  border-radius: 8px;
  font-weight: bold;
  margin-bottom: 15px;
  text-align: center;
}

.message-box.success {
  background-color: #d4edda;
  color: #155724;
  border: 2px solid #c3e6cb;
}

.message-box.error {
  background-color: #f8d7da;
  color: #721c24;
  border: 2px solid #f5c6cb;
}



/* Popup arka planı */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

/* Popup kutusu */
.popup {
  background: #fff;
  border-radius: 12px;
  padding: 25px 35px;
  width: 350px;
  text-align: center;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
  animation: popupFadeIn 0.3s ease-in-out;
}

/* Başlık */
.popup h2 {
  margin-bottom: 20px;
  color: #333;
  font-size: 20px;
}

/* Buton */
.popup button {
  padding: 10px 20px;
  border: none;
  background: #4caf50;
  color: white;
  font-size: 16px;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s ease;
}

.popup button:hover {
  background: #45a049;
}

/* Animasyon */
@keyframes popupFadeIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Popup ortak stil */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup {
  background: #fff;
  padding: 20px;
  border-radius: 12px;
  text-align: center;
  max-width: 400px;
  width: 100%;
  box-shadow: 0 5px 20px rgba(0,0,0,0.3);
}

.popup button {
  margin-top: 15px;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  background: #4CAF50;
  color: #fff;
  cursor: pointer;
}
